import * as React from 'react';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import './Table.css';

function createData( name , trackingId, date, status) {
  return { name , trackingId, date, status };
}

const rows = [
  createData('Frozen yoghurt', 18908424, "2 March 2022", "Approved"),
  createData('Ice cream sandwich', 18920132, "3 March 2022", "Pending"),
  createData('Eclair', 1890232, "2 March 2022", "Pending"),
  createData('Cupcake', 18904912, "2 March 2022", "Delivered"),
];

const makeStyles = (status) => {
  if(status === 'Approved'){
    return {
      background: 'rgb(145 254 159 / 47%)',
      color: 'green'
    }
  }else if(status === 'Pending'){
    return {
      background: '#ffadad8f',
      color: 'red'
    }
  }else{
    return {
      background: '#59bfff',
      color: 'white'
    }
  }
}


export default function DenseTable() {
  return (
    <div className="Table">
        <TableContainer component={Paper}
        style={{boxShadow: '0px 13px 20px 0px #80808029'}}
        >
      <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
        <TableHead>
          <TableRow>
            <TableCell>Product</TableCell>
            <TableCell align="left">TrackingId</TableCell>
            <TableCell align="left">Date</TableCell>
            <TableCell align="left">Status</TableCell>
            <TableCell align="left"></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow
              key={row.name}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row">
                {row.name}
              </TableCell>
              <TableCell align="left">{row.trackingId}</TableCell>
              <TableCell align="left">{row.date}</TableCell>
              <TableCell align="left">
                <span className="status" style={makeStyles(row.status)}>
                {row.status}
                </span>
              </TableCell>
              <TableCell align="left" className="Details">Detail</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
    </div>
  );
}